	week4_after_leg1: (week: week4, posFirst: 0, posLast: -14500px, timer:3, step: 25, img: week4_after_leg1, count: infinite),//곰 - 아빠 - 3월
	week4_after_leg2: (week: week4, posFirst: 0, posLast: -14500px, timer:3, step: 25, img: week4_after_leg2, count: infinite),//곰 - 아빠 - 3월
	week4_after_leg3: (week: week4, posFirst: 0, posLast: -14500px, timer:3, step: 25, img: week4_after_leg3, count: infinite),//곰 - 아빠 - 3월
