@each $label, $map in $week-list-after {
	$weekStep: map-get($map, step)-1;
	$weekTimer: 100/12*$weekStep/100;
	$comStep: map-get($map, complete)-1;
	$comTimer: 100/12*$comStep/100;

	@keyframes #{$label}_after {
		0% { background-position: 0 0}
		//#{($weekTimer/($weekTimer+1))*100}%{ background-position: -#{580*$weekStep}px 0;} 1초 딜레이 제거
		100% { background-position: -#{580*$weekStep}px 0;}
	}
	@keyframes #{$label}_complete1 {
		0% { background-position: 0 0}
		//#{($comTimer/($comTimer+1))*100}%{ background-position: -#{580*$comStep}px 0;} 1초 딜레이 제거
		100% { background-position: -#{580*$comStep}px 0;}
	}

	.monster {
		&.#{$label}_complete1 {
			animation: #{$label}_complete1 #{$comTimer}s steps(#{$comStep}, end) infinite;
			background: url("/assets/images/sprite/#{$label}_complete1.png") no-repeat 0 0;
		}
		@for $leg from 1 through 6 {
			&.#{$label}_after_leg#{$leg} {
				animation: #{$label}_after #{$weekTimer}s steps(#{$weekStep}, end) infinite;
				background: url("/assets/images/sprite/#{$label}_after_leg#{$leg}.png") no-repeat 0 0;
			}
		}
	}
}


